export default {
	data() {
		return {
			numList: [
				{
					name: '提交订单'
				},
				{
					name: '付款'
				}
			],
			num: 0,
			amount: 0,
			orderName: '',
			paytype: 'alipay' //支付类型
		};
	},
	onLoad(e) {},
	methods: {
		NumSteps() {
			this.num = this.num == this.numList.length - 1 ? 0 : this.num + 1;
		},
		doDeposit() {
			//模板模拟支付，实际应用请调起微信/支付宝
			uni.showLoading({
				title: '支付中...'
			});
			setTimeout(() => {
				uni.hideLoading();
				uni.showToast({
					title: '支付成功'
				});
				setTimeout(() => {
					uni.redirectTo({
						url: '../../pay/success/success?amount=' + this.amount
					});
				}, 300);
			}, 700);
		}
	}
};
